@import url("https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap");
:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 0.75rem;
  font-family: "Kumbh Sans", sans-serif;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-image: linear-gradient(#af67e9, #6565e7);
  color: #787887;
  overflow-y: hidden;
  overflow-x: hidden;
}

main {
  display: grid;
  place-content: center;
  min-height: 100vh;
  padding-inline: 1.8333333333rem;
}

footer {
  background-color: #6565e7;
  text-align: center;
}
footer a {
  color: #3e52a3;
}

.card {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding-inline: 1.6666666667rem;
  padding-top: 10.8333333333rem;
  padding-bottom: 3.1666666667rem;
  max-width: 27.5rem;
  max-height: 59.5833333333rem;
  background-color: var(--white);
  background-image: url(/app/asset/images/bg-pattern-mobile.svg);
  background-repeat: no-repeat;
  background-position-x: center;
  border-radius: 1.25rem;
}
.card__image {
  position: absolute;
  top: -8.75rem;
  width: 19.5833333333rem;
}
.card__image .box {
  display: none;
}
.card__heading {
  padding-bottom: 1rem;
  font-size: 2rem;
  font-weight: 700;
  text-align: center;
  color: #4a4b5e;
}
.card__text--container {
  overflow: hidden;
}
.card__text--container .accordion {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-block: 0.8333333333rem;
  padding-inline: 0;
  border: none;
  background-color: transparent;
  width: 100%;
  outline: none;
  font-weight: 400;
  color: #4a4b5e;
  transition: all 700ms;
}
.card__text--container .accordion span {
  font-size: 1.0833333333rem;
}
.card__text--container .accordion.active {
  font-weight: 700;
}
.card__text--container .panel {
  font-size: 0.9166666667rem;
  max-height: 0;
  text-align: justify;
  overflow: hidden;
  transition: all 725ms ease;
  padding: 0;
  border-bottom: 1px solid #787887;
}
.card__text--container .panel.expand {
  max-height: 6.6666666667rem;
  padding-bottom: 1rem;
}
.card__text--container .fa-chevron-down {
  transform: rotate(0deg);
  transition: transform 350ms linear;
}
.card__text--container .fa-chevron-down.open {
  transform: rotate(-180deg);
  transition: transform 350ms linear;
}

@media (min-width: 40em) {
  .card {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    align-items: center;
    min-width: 87.5rem;
    height: 42.0833333333rem;
    padding: 0;
    background-image: url(/app/asset/images/bg-pattern-desktop.svg);
    background-repeat: no-repeat;
    background-position-x: -47.3333333333rem;
    background-position-y: -20.8333333333rem;
  }
  .card__image {
    position: relative;
    top: 0;
    flex-basis: 45%;
    width: 50rem;
  }
  .card__image picture {
    overflow: hidden;
  }
  .card__image picture .image {
    margin-left: -6.25rem;
  }
  .card__image .box {
    display: block;
    position: absolute;
    top: 11.6666666667rem;
    left: -7.75rem;
    z-index: 2;
  }
  .card__text {
    flex-basis: 55%;
    height: 35.4166666667rem;
    padding-right: 3.75rem;
    padding-block: 4.5833333333rem;
  }
  .card__heading {
    padding-bottom: 2.0833333333rem;
    font-size: 2.8333333333rem;
    text-align: left;
  }
  .card__text--container .accordion {
    padding-block: 1rem;
  }
  .card__text--container .accordion span {
    font-size: 1.3333333333rem;
  }
  .card__text--container .panel {
    font-size: 1.0833333333rem;
  }
}
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.2;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

/*
1. Use a more-intuitive box-sizing model.
*/
*, *::before, *::after {
  box-sizing: border-box;
}

/*
2. Remove default margin
*/
* {
  margin: 0;
}

/*
3. Allow percentage-based heights in the application
*/
html, body {
  height: 100%;
}

/*
Typographic tweaks!
4. Add accessible line-height
5. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

/*
6. Improve media defaults
*/
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/*
7. Remove built-in form typography styles
*/
input, button, textarea, select {
  font: inherit;
}

/*
8. Avoid text overflows
*/
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/*
9. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}